<template>
	<view  style="" >
	<view class="topic">
		<view  :class="{'show':isShow}" class="t1" >
			<view class="t2">
				1
			</view>
		</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				isShow:false
			}
		},
		onLoad() {
			setTimeout(()=>{this.isShow=true},200),
			uni.setNavigationBarTitle({
			    title: this.title
			});
		},
		methods: {

		}
	}
</script>

<style lang="scss">
	.topic{
		position: absolute;
		background-color: rgba($color: #4ff1ff, $alpha: 0.7);
		height: 100%;
		padding:10rpx 20rpx;
		box-sizing: border-box;
		.show{transition: 0.3s;
				opacity: 1!important;
				left: 0rpx!important;
		}	
		.t1{
			background-color: rgba($color: #0fd7ff, $alpha: 0.5);
			height: 120rpx;
			width: 220rpx;
			border-radius: 30rpx;
			display:table-cell;
			vertical-align: middle; 
			
			// animation: show 1s   forwards  ;
			position: relative;
			opacity: 0;left: -40rpx;
			.t2{
				background-color: rgba($color: #0fd7ff, $alpha: 1);
				height: 100rpx;
				width: 200rpx;
				line-height: 100rpx;
				text-align: center;
				border-radius: 20rpx;
				margin: 0 auto;
			}
		}
	}
	
	@keyframes show{
		0%{opacity: 0;left: -40rpx;}
		
		100%{opacity: 1;left: 0rpx;}
		 
	}
	
</style>
